<template>
  <div>
    <Header @change="updateusername" />
    <ProfilePage :value="new_username" />
    <Footer />
  </div>
</template>

<script>
import Header from "../components/header.vue";
import ProfilePage from "../components/profile.vue";
import Footer from "../components/footer.vue";
import { ref } from "vue";

export default {
  components: { Header, ProfilePage, Footer },
  name: "Profile",

  setup() {
    const new_username = ref(false);
    function updateusername() {
      new_username.value = true;
    }

    return { updateusername, new_username };
  },
};
</script>

