<template>
  <nav
    class="navbar container has-background-light"
    role="navigation"
    aria-label="main navigation"
  >
    <div class="navbar-brand">
      <a class="navbar-item">
        <img src="@/assets/logo.png" />
        <span
          class="navbar-item is-size-3 is-size-6-mobile has-text-weight-bold"
        >
          <router-link to="/"
            ><span class="has-text-grey"
              >The Samosa Dictionary</span
            ></router-link
          >
        </span>
      </a>

      <a
        role="button"
        class="navbar-burger"
        aria-label="menu"
        aria-expanded="false"
        @click="toggleMenu"
      >
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
        <span aria-hidden="true"></span>
      </a>
    </div>

    <div
      class="navbar-menu has-background-light"
      :class="mobileMenu && 'is-active'"
    >
      <div class="navbar-start"></div>

      <div class="navbar-end">
        <div class="navbar-item">
          <div class="buttons">
            <div v-if="!AuthState.isAuthenticated">
              <a class="button is-primary is-light" @click="login">
                <strong>Sign up</strong>
              </a>
            </div>
            <div v-else>
              <a class="button is-primary is-light">
                <router-link
                  :to="{ path: '/profile/' + AuthState.user.nickname }"
                  >{{ AuthState.user.nickname }}</router-link
                ></a
              >
              <a class="button is-primary is-light">
                <router-link to="/add">Add A Slang</router-link>
              </a>
              <a class="button is-danger is-light" @click="logout">Log out</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import { ref, watch } from "vue";
import { useAuth0, AuthState } from "@/utils/useAuth0";
import { useRoute } from "vue-router";
const { login, logout, initAuth } = useAuth0(AuthState);

initAuth();
export default {
  name: "Header",
  emits: ["change"],
  setup(props, context) {
    const mobileMenu = ref(false);
    const route = useRoute();

    watch(
      () => route.params.username,
      (username) => {
        context.emit("change", username);
      }
    );

    function toggleMenu() {
      mobileMenu.value = !mobileMenu.value;
    }

    return {
      mobileMenu,
      toggleMenu,
      login,
      logout,
      AuthState,
      useAuth0,
    };
  },
};
</script>

<style scoped></style>
