<template>
  <div>
    <div class="columns">
      <div class="column is-3"></div>
      <div class="column is-6 ml-3">
        <form class="box">
          <div class="field">
            <div class="control">
              <input
                class="input"
                type="text"
                placeholder="* Slang"
                required
                v-model="slang"
              />
            </div>
          </div>

          <div class="field">
            <div class="control">
              <textarea
                class="textarea"
                placeholder="* Give Your Slang Some Definition"
                required
                v-model="definition"
              ></textarea>
            </div>
          </div>

          <div class="field">
            <div class="control">
              <textarea
                class="textarea"
                placeholder="* An Example Of Your Slang Being Used"
                rows="2"
                required
                v-model="example"
              ></textarea>
            </div>
          </div>

          <div class="field">
            <div class="control">
              <input
                class="input"
                type="text"
                placeholder="Optional: Url To Any Relevant Gif Or Image"
                v-model="image"
              />
            </div>
          </div>

          <button
            class="button is-success is-light is-fullwidth"
            @click.prevent="submitSlang"
          >
            Add My Slang
          </button>
        </form>

        <div
          class="notification is-warning is-light mt-3 box"
          v-show="notification"
        >
          <button class="delete" @click="notification = !notification"></button>
          Thanks For Contributing!
        </div>

        <div
          class="notification is-danger is-light mt-3 box"
          v-show="error_notification"
        >
          <button
            class="delete"
            @click="error_notification = !error_notification"
          ></button>
          Please Fill The Required Fields Marked With *.
        </div>
      </div>
      <div class="column is-3"></div>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import { AuthState } from "@/utils/useAuth0";

export default {
  name: "Form",

  setup() {
    const slang = ref("");
    const definition = ref("");
    const example = ref("");
    const image = ref(null);
    const notification = ref(false);
    const error_notification = ref(false);

    function submitSlang() {
      if (
        slang.value === "" ||
        definition.value === "" ||
        example.value === ""
      ) {
        error_notification.value = true;
      } else {
        fetch("https://samosadict.pythonanywhere.com/create_definition", {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            username: AuthState.user.nickname,
            word: slang.value,
            definition: definition.value,
            example: example.value,
            image: image.value,
            fullname: AuthState.user.name,
          }),
        });
        notification.value = true;
        (slang.value = ""),
          (definition.value = ""),
          (example.value = ""),
          (image.value = "");
      }
    }

    return {
      submitSlang,
      slang,
      definition,
      example,
      image,
      notification,
      error_notification,
      AuthState,
    };
  },
};
</script>

<style scoped></style>
