<template>
  <div>
    <div class="card mt-2 ml-3" v-show="visible">
      <header class="card-header">
        <p class="card-header-title">{{ word }}</p>
        <!-- <p
          class="card-header-title has-background-primary-light"
          v-show="copied"
        >
          Link Copied!
        </p> -->
      </header>

      <div class="card-content">
        <div class="content has-text-justified">
          <p>
            {{ definition }}
          </p>

          <p class="is-italic" v-html="example"></p>

          <div class="card-image" v-if="image !== null">
            <figure class="image">
              <img :src="image" alt="supporting gif or image" />
            </figure>
          </div>

          <hr />

          <div class="level">
            <div class="level-left">
              <p class="subtitle is-6 has-text-grey has-text-weight-semibold">
                Contributed by
                <router-link :to="{ path: '/profile/' + username }">
                  {{ username }}
                </router-link>
              </p>
            </div>

            <div class="level-right">
              <span>
                <input
                  class="input is-small"
                  type="text"
                  placeholder="Text input"
                  :value="'https://akulchhillar.github.io/#/slang/' + id"
                  :id="id"
                  readonly
                />
              </span>
              <span
                class="icon mx-1"
                title="Click To Copy Url"
                @click="copy(id)"
              >
                <i class="fas fa-share-alt"></i>
              </span>
              <span
                class="icon mx-1"
                v-if="profile"
                title="Double Click Delete"
                @dblclick="delete_post(id)"
                :v-show="visible"
              >
                <i class="fas fa-trash"></i>
              </span>
            </div>
          </div>
          <footer
            class="card-footer has-background-primary-light"
            v-show="copied"
          >
            <div class="card-footer-item">Link Copied!</div>
          </footer>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
export default {
  name: "Card",
  emits: ["updateposts"],
  props: {
    word: {
      default: "",
    },
    definition: { default: "" },
    example: { default: "" },
    username: { default: "" },
    image: {
      default: null,
    },
    profile: {
      default: false,
    },
    id: {
      default: null,
    },
  },

  setup(props, context) {
    const copied = ref(false);
    const visible = ref(true);
    function copy(id) {
      let copyText = document.getElementById(id);

      copyText.select();
      copyText.setSelectionRange(0, 99999);
      navigator.clipboard.writeText(copyText.value);

      setTimeout(() => {
        copied.value = true;
        setTimeout(() => {
          copied.value = false;
        }, 750);
      }, 1);
    }

    function delete_post(id) {
      fetch("https://samosadict.pythonanywhere.com/delete", {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ id: id }),
      }).then(() => {
        visible.value = false;
        context.emit("updateposts", true);
      });
    }

    return { copy, delete_post, visible, copied };
  },
};
</script>

<style scoped></style>
