<template>
  <div>
    <Header />
    <div class="columns">
      <div class="column is-3"></div>

      <div class="column is-6">
        <Error v-show="error" />
        <Loader v-show="loading" />
        <Card
          :word="word"
          :username="username"
          :definition="definition"
          :image="image"
          :id="id"
          v-show="page"
        />
      </div>
      <div class="column is-3"></div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Header from "../components/header.vue";
import Card from "../components/card.vue";
import Footer from "../components/footer.vue";
import Loader from "../components/loader.vue";
import Error from "../components/error.vue";

import { useRoute } from "vue-router";
import { ref, onBeforeMount, reactive, toRefs } from "vue";
export default {
  components: { Header, Card, Footer, Loader, Error },
  name: "Word",
  setup() {
    const id = ref(null);
    const route = useRoute();
    const page = ref(true);
    const loading = ref(false);
    const error = ref(false);
    id.value = route.params.word;
    const word = reactive({
      username: null,
      word: null,
      definition: null,
      example: null,
      image: null,
      id: null,
    });

    onBeforeMount(() => {
      loading.value = true;
      page.value = false;
      fetch("https://samosadict.pythonanywhere.com/word", {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ id: id.value }),
      })
        .then((response) => response.json())
        .then((json) => {
          if (json["status"] === 400) {
            error.value = true;
            loading.value = false;
            page.value = false;
          } else {
            word.username = json["results"][0]["username"];
            word.word = json["results"][0]["word"];
            word.definition = json["results"][0]["definition"];
            word.example = json["results"][0]["example"];
            word.image = json["results"][0]["image"];
            word.id = json["results"][0]["id"];
            loading.value = false;
            page.value = true;
          }
        });
    });

    return { ...toRefs(word), loading, error, page };
  },
};
</script>

