<template>
  <div>
    <div class="columns container">
      <div class="column is-3"></div>
      <div class="column is-6">
        <Loader v-show="loading" />

        <article class="box ml-3 card" v-show="page">
          <h1 class="title has-text-centered">Slangs By {{ username }}</h1>
        </article>

        <div v-for="post in posts" :key="post.id" v-show="page">
          <Card
            :profile="deleteicon"
            :word="post.word"
            :definition="post.definition"
            :example="post.example"
            :image="post.image"
            :username="post.username"
            :id="post.id"
            @updateposts="refresh"
          />
        </div>
      </div>
      <div class="column is-3"></div>
    </div>
  </div>
</template>

<script>
import Card from "../components/card.vue";
import { useRoute } from "vue-router";
import { ref, onBeforeMount, watch } from "vue";
import { AuthState } from "@/utils/useAuth0";
import Loader from "../components/loader.vue";

export default {
  name: "ProfilePage",
  components: { Card, Loader },
  props: { value: { default: false } },

  setup(props) {
    const username = ref(null);
    const userimg = ref(null);
    const deleteicon = ref(false);
    const posts = ref([]);
    const fullname = ref(null);
    const route = useRoute();
    const route2 = useRoute();
    const loading = ref(false);
    const page = ref(true);

    watch(props, () => profile_post());

    function refresh() {
      profile_post();
    }

    function profile_post() {
      loading.value = true;
      page.value = false;
      username.value = route.params.username;
      userimg.value =
        "https://avatars.dicebear.com/api/personas/" + username.value + ".svg";

      if (
        AuthState.isAuthenticated &&
        AuthState.user.nickname == username.value
      ) {
        deleteicon.value = true;
      }

      fetch("https://samosadict.pythonanywhere.com/profile", {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ username: route2.params.username }),
      })
        .then((response) => response.json())
        .then((json) => {
          loading.value = false;
          page.value = true;
          posts.value = json["results"];
          fullname.value = json["results"][0]["fullname"];
        });
    }

    onBeforeMount(() => {
      profile_post();
    });

    return {
      username,
      userimg,
      AuthState,
      deleteicon,
      posts,
      fullname,
      loading,
      page,
      refresh,
    };
  },
};
</script>

<style scoped></style>
