<template>
  <div>
    <section class="section is-large has-text-centered container p-0">
      <h1 class="title has-text-grey is-size-2-mobile">
        Create Acronyms, Share Experiences
      </h1>
      <div class="columns">
        <div class="column is-3"></div>
        <div class="column is-6">
          <input
            class="input is-large card ml-2"
            type="text"
            placeholder="Type any acronym and hit enter..."
            @keyup.enter="search"
            v-model="search_value"
          />
          <div class="mt-6" v-show="!results">
            <h1
              class="
                subtitle
                has-text-left
                p-0
                ml-3
                mt-0
                has-text-weight-bold
                is-size-6
              "
            >
              Recently Added
            </h1>

            <Loader v-show="loading" />

            <Card
              :word="word"
              :definition="definition"
              :example="example"
              :username="username"
              :image="image"
              :id="id"
              v-show="!loading"
              class="pre-formatted"
            />
          </div>

          <div v-show="results" v-for="slang in words" :key="slang.id">
            <Loader v-show="search_results" />
            <Card
              :word="slang.word"
              :definition="slang.definition"
              :example="slang.example"
              :username="slang.username"
              :image="slang.image"
              :id="slang.id"
              v-show="!search_results"
            />
          </div>
        </div>
        <div class="column is-3"></div>
      </div>
    </section>
  </div>
</template>

<script>
import Card from "../components/card.vue";
import Loader from "../components/loader.vue";
import { ref, onBeforeMount } from "vue";
export default {
  components: { Card, Loader },
  name: "Section",

  setup() {
    const word = ref(null);
    const definition = ref(null);
    const example = ref(null);
    const username = ref(null);
    const image = ref(null);
    const search_value = ref(null);
    const id = ref(null);

    const results = ref(false);
    const words = ref([]);
    const loading = ref(false);
    const search_results = ref(false);

    onBeforeMount(() => {
      loading.value = true;
      fetch("https://samosadict.pythonanywhere.com/latest")
        .then((response) => response.json())
        .then((json) => {
          word.value = json["results"][0]["word"];
          definition.value = json["results"][0]["definition"];
          example.value = json["results"][0]["example"];
          username.value = json["results"][0]["username"];
          image.value = json["results"][0]["image"];
          id.value = json["results"][0]["id"];

          loading.value = false;
        });
    });

    function search() {
      search_results.value = true;
      fetch("https://samosadict.pythonanywhere.com/entry", {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ word: search_value.value }),
      })
        .then((response) => response.json())
        .then((json) => {
          words.value = json["results"];
          results.value = true;
          search_results.value = false;
        });
    }
    return {
      word,
      definition,
      example,
      username,
      search,
      search_value,
      results,
      words,
      image,
      loading,
      search_results,
      id,
    };
  },
};
</script>

<style scoped>
.pre-formatted {
  white-space: pre-wrap;
}
</style>
